<template>
  <div class="wrapper">
    <div
      class="player-panel"
      v-bind:class="{
        active:activePlayer==0 && !isWinner,
        winner:isWinner && activePlayer==0
      }"
    >
      <div class="player-name">{{getName(0)}}</div>
      <div class="player-score">{{playerScore[0]}}</div>
      <div class="player-current-box">
        <div class="player-current-label">Current</div>
        <div class="player-current-score">{{currentScore[0]}}</div>
      </div>
    </div>

    <div
      class="player-panel"
      v-bind:class="{
        active:activePlayer==1 && !isWinner,
        winner:isWinner && activePlayer==1
      }"
    >
      <div class="player-name">{{getName(1)}}</div>
      <div class="player-score">{{playerScore[1]}}</div>
      <div class="player-current-box">
        <div class="player-current-label">Current</div>
        <div class="player-current-score">{{currentScore[1]}}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "player",
  data() {
    return {};
  },
  props: {
    playerScore: { type: Array, default: [0, 0] },
    currentScore: { type: Array, default: [0, 0] },
    activePlayer: { type: Number, default: 0 },
    finalScore: { type: Number, default: 0 },
    isWinner: { type: Boolean, default: false }
  },
  methods: {
    getName: function(index) {
      var defaultname = "Player " + (index + 1);
      if (this.isWinner && this.activePlayer == index) {
        defaultname = "Winner";
      }
      return defaultname;
    }
  }
};
</script>


