<template>
    <div class="wraper-popup" v-bind:class="getIsPopup">
        <div class="rule">
            <p>
               Bạn có chắc muốn bắt đầu game mới  ?
            </p>
            <button v-on:click="confirmPopup" href="#" class="popupLink">Bắt đầu!</button>
        </div>
    </div>
</template>
<script>
export default {
    name:'Popup',
    props:{
        isOpenPopup:{type:Boolean, default:false}
    },
    computed:{
        getIsPopup:function(){
            return this.isOpenPopup ? "open-popup":""
        }
    },
    methods:{
        confirmPopup:function(){
            this.$emit("confirmPopup");
        }
    }

}
</script>
<style scoped>
.wraper-popup{
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background:rgba(0,0,0,0.4);
    padding: 20px ;
    opacity: 0;
    visibility: hidden;
}
.open-popup{
    opacity: 1;
    visibility: visible;
    transform: translate(50,50) scale(1);
}
.rule{
    background-color: white;
    width: 350px;
    text-align: center;
    margin: 0 auto;
    padding: 30px;
    transition: all 0.3 ease;
}
.open-popup .rule{
    transform: translate(50,50) scale(1);
}
.rule p{
    padding-bottom: 10px;
}
.rule button{
    padding: 10px;
    background-color: black;
    color: white;
    cursor: pointer;
}
.rule button:hover{
    background-color: teal;
    transition: all ease 0.3;
}
</style>


