<template>
  <div class="wrapper-dice">
    <div id="dice-1" class="dice">
      <div class="spinner" v-bind:class="getDiceOne">
        <div class="face1">1</div>
        <div class="face2">2</div>
        <div class="face3">3</div>
        <div class="face4">4</div>
        <div class="face5">5</div>
        <div class="face6">6</div>
      </div>
    </div>
    <div id="dice-2" class="dice">
      <div class="spinner" v-bind:class="getDiceTwo">
        <div class="face1">1</div>
        <div class="face2">2</div>
        <div class="face3">3</div>
        <div class="face4">4</div>
        <div class="face5">5</div>
        <div class="face6">6</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Dice",
  data() {
    return {};
  },
  props: {
    dicesValue: { type: Array, default: [0, 0] }
  },
  computed: {
    getDiceOne: function() {
      return "dice-" + this.dicesValue[0];
    },
    getDiceTwo: function() {
      return "dice-" + this.dicesValue[1];
    }
  }
};
</script>


