<template>
  <div class="wrapper-control">
    <button v-on:click="newgameHandle" class="control btn-new">
      <i class="ion-ios-plus-outline"></i>New game
    </button>
    <button v-on:click="rollDiceHandle" class="control btn-roll">
      <i class="ion-ios-loop"></i>Roll dice
    </button>
    <button v-on:click="holdHandle" class="control btn-hold">
      <i class="ion-ios-download-outline"></i>Hold
    </button>
    <input
      v-bind:disabled="isPlay"
      v-bind:value="finalScore"
      v-on:input="inputHandle"
      type="number"
      placeholder="Final score"
      class="final-score"
    />
  </div>
</template>

<script>
export default {
  name: "Control",
  data() {
    return {
    };
  },
  methods: {
    newgameHandle: function() {
      this.$emit("newgameHandle");
    },
    rollDiceHandle: function() {
      console.log("rolldice");
      this.$emit("rollDiceHandle");
    },
    holdHandle: function() {
      this.$emit("holdHandle");
    },
    inputHandle: function(event) {
      console.log(event.target.value);
      this.$emit("inputHandle",event.target.value);
    }
  },
  props: {
    isPlay: { type: Boolean, default: false },
    finalScore: { type: Number, default: 0 }
  }
};
</script>


